%list {
  padding: 0;
  margin: 0px;

  list-style: none;
  font-style: normal;
}

%button {
  display: inline-block;
  padding: 13px 36px;

  min-width: 130px;
  min-height: 44px;

  color: $white-color;
  text-transform: uppercase;
  background-color: transparent;

  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  border: 1px solid $white-color;

  border-radius: 5px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    min-width: 136px;
    padding: 13px 38px;
  }
}

%flex-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

%is-none {
  display: none;
}

%is-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

%font-title {
  text-transform: uppercase;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}

%font-description {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
}


%font-nav {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: $white-color;
  cursor: pointer;
}
  
%button-modal {
  width: 136px;
  height: 44px;
  background-color: $white-color;
  border-radius: 5px;
  border: 1px solid $black-color;
  box-sizing: border-box;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 10px;
  color: $black-color;
  text-transform: uppercase;
  transition: 500;
  cursor: pointer;

  @include transit(background-color, color, border-color);

  @media screen and (min-width: 1024px) {
    width: 155px;
    //margin-right: 20px;
  }

  &:hover,
  &:focus {
    background-color: $btn-accent-color;
    color: $white-color;
    border-color: $btn-accent-color;
  }
}
